body {
	margin: 0;
	font-family: 'Inter', sans-serif;
	overflow-x: hidden;
}

#root {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	height: 100%;
}

h1, h2, h3 {
	margin: 0;
}

:root[data-theme="light"] {
  --accent-color: #fafafa;
	--color: black;
	--bg-color: #ffffff;
	--border-color: #C8C8C8;
	--arrow-name: 'arrow.png';
}

:root[data-theme="dark"] {
  --accent-color: #222222;
	--color: white;
	--bg-color: #2B2A2A;
	--border-color: #434343;
	--arrow-name: 'arrow-white.png';
}

body {
  background: var(--accent-color);
	color: var(--color);
}
